import classes from './OtherPage.module.css';

const EnterRoom = (props) => {
    return (
        <div className={classes.inputPage}>
            <div className={classes.windowInput}>
                <div className={classes.title}>Під'єднатися до кімнати</div>
                <div>
                    <input placeholder="Код підключення до кімнати" type="text" className={classes.inputData} name="name" />
                    <div className={classes.error}>Невірний код</div>
                </div>
                <button className={classes.submit}>Під'єднатися</button>
            </div>
        </div>
    )
}

export default EnterRoom;